import React from 'react'
import { Link, Outlet } from 'react-router-dom'

const Portfolio = () => {
    return (
        <>
            <div className="container">
                <div className="my-3 fs-2 heading text-center border border-dark">
                    Portfolio
                </div>
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                    <div className="container ">

                        <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav me-auto mb-2 mb-lg-0 d-flex justify-content-around">
                                <li className="nav-item">
                                    <Link className="nav-link" aria-current="page" to="/portfolio/all">All</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/portfolio/coded">Coded</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/portfolio/designed">Designed</Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                </nav>
                <Outlet />
            </div>
        </>
    )
}

export default Portfolio
